@import "_normalize";
@import "_variables";

//Google Fonts
@import url('https://fonts.googleapis.com/css?family=Inconsolata:400,700|Open+Sans:400,600,700&display=swap&subset=latin-ext');



html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  color: #FFFFFF;
  background: #212121;
}

.header {
  background-image: url('../assets/img/header__background.png');
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  height: 100vh;
  display: grid;
  align-content: center;
  justify-items: center;
  position: relative;

  &__content {
    height: 416px;
    max-width: 1364px;
    padding: 0 60px;
    width: 100%;
  }

  &__heading {
    font-family: $inconsolata;
    font-size: 68px;
    margin-bottom: 30px;

    @media (max-width: $tablet) {
      font-size: 48px;
    }

    @media (max-width: $mobile) {
      font-size: 28px;
    }
  }

  &__text {
    font-family: $open-sans;
    max-width: 674px;
    width: 100%;
    font-size: 21px;
    line-height: 30px;

    @media (max-width: $tablet) {
      font-size: 16px;
      line-height: 25px;
    }

    @media (max-width: $mobile) {
      font-size: 14px;
      line-height: 23px;
    }
  }

  &__button {
    font-family: $inconsolata;
    font-size: 21px;
    font-weight: bold;
    text-decoration: none;
    color: #212121;
    background: #FFFFFF;
    border-radius: 100px;
    padding: 29px 61px;
    transition: background .2s ease-in-out;

    &:hover {
      background: rgb(197, 145, 0);
    }

    @media (max-width: $tablet) {
      font-size: 16px;
    }
  }

  &__cta {
    margin-top: 70px;
  }

  &__scroller {
    border: 5px solid #FFFFFF;
    border-radius: 100px;
    height: 68px;
    width: 44px;
    position: absolute;
    bottom: 60px;

    @media (max-width: $small-desktop) {
      display: none;
    }
  }

  &__scroll {
    background: #FFFFFF;
    border-radius: 100px;
    height: 20px;
    width: 12px;
    position: absolute;
    top: 32px;
    left: 50%;
    transform: translateX(-50%);
    animation: scrolling 1s ease-in-out infinite;

    @keyframes scrolling {
      0% {
        top: 32px;
      }
      100% {
        top: 16px;
      }
    }
  }

  &__date {
    color: rgba(255,255,255,0.5);
    position: absolute;
    bottom: 60px;
    right: 60px;
    font-family: $inconsolata;
    font-size: 21px;
    font-weight: bold;

    @media (max-width: $tablet) {
      font-size: 16px;
      bottom: 30px;
    }

    @media (max-width: $mobile) {
      font-size: 14px;
      line-height: 23px;
    }
  }

  .navigation {
    background: #212121;
    position: fixed;
    top: 0;
    width: 100%;
    display: grid;
    grid-auto-flow: column;
    align-items: center;
    padding: 25px 0;
    //height: 110px;
    z-index: 9999;

    @media (max-width: $small-desktop) {
      grid-auto-flow: row;
    }

    &__logo {
      padding-left: 140px;
      text-decoration: none;
      color: #FFFFFF;
      font-family: $inconsolata;
      font-size: 48px;
      font-weight: bold;

      @media (max-width: $small-desktop) {
        padding-left: 60px;
      }

      @media (max-width: $tablet) {
        font-size: 38px;
      }

      @media (max-width: $mobile) {
        font-size: 28px;
      }
    }

    &__list {
      list-style-type: none;
      justify-self: end;
      margin-right: 140px;
    }

    @media (max-width: $small-desktop) {
      &__list {
      display: none;
      justify-self: center;
      text-align: center;
      margin-right: 0;
      padding: 0;
      }

      .navigation__list--visible {
        display: block;
      }
    }

    &__item {
      display: inline-block;
      margin-right: 60px;

      @media (max-width: $small-desktop) {
        display: block;
        margin-right: 0;
        margin-top: 40px;
      }
    }

    &__item:last-child {
      margin-right: 0;

      @media (max-width: $small-desktop) {
        margin-top: 60px;
      }
    }

    &__link {
      text-decoration: none;
      color: #FFFFFF;
      font-family: $inconsolata;
      font-size: 21px;
      font-weight: normal;
      transition: color .2s ease-in-out, font-weight .2s ease-in-out;

      &--button {
        border: 3px solid #FFFFFF;
        border-radius: 100px;
        padding: 14px 30px;
        font-weight: normal;
        transition: background .2s ease-in-out, color .2s ease-in-out, font-weight .2s ease-in-out;

        &:hover {
          background: #FFFFFF;
          color: #212121;
          font-weight: bold;
        }
      }

      &:hover:not(.navigation__link--button) {
        font-weight: bold;
        color: rgb(197, 145, 0);
      }
    }

    &__hamburger {
      font-family: $inconsolata;
      font-size: 21px;
      color: #FFFFFF;
      background: none;
      border: none;
      width: 24px;
      height: 24px;
      position: absolute;
      top: 45px;
      right: 60px;
      cursor: pointer;

      @media (min-width: $small-desktop) {
        display: none;
      }

      @media (max-width: $tablet) {
        top: 30px;
      }
    }
  }
}

.main {
  width: 100%;

  .cards {
    width: 100%;
    display: grid;
    justify-content: center;

    &__heading {
      font-family: $inconsolata;
      font-size: 48px;
      margin-top: 120px;
      margin-bottom: 24px;
      text-align: center;

      @media (max-width: $mobile) {
        font-size: 28px;
        margin-bottom: 12px;
      }
    }

    &__description {
      font-family: $open-sans;
      font-weight: 21px;
      text-align: center;
      margin-bottom: 60px;
      color: rgba(255,255,255,0.5);

      @media (max-width: $mobile) {
        font-size: 14px;
        line-height: 23px;
      }
    }

    &__container {
      max-width: 1304px;
      width: 100%;
      display: grid;
      grid-template-columns: 1fr 1fr;
      justify-content: center;
      grid-gap: 60px;

      @media (max-width: $small-desktop) {
        grid-template-columns: 1fr;
      }
    }

    .card {
      max-width: 652px;
      width: 100%;
      min-height: 852px;
      background: #FFFFFF;
      color: #212121;
      position: relative;

      @media (max-width: $tablet) {
        min-height: 752px;
      }

      &__type {
        display: block;
        font-family: $open-sans;
        font-weight: bold;
        font-size: 21px;
        opacity: .5;
        margin: 60px 0 0 60px;

        @media (max-width: $tablet) {
          font-size: 16px;
        }

        @media (max-width: $mobile) {
          font-size: 14px;
        }
      }

      &__title {
        font-family: $inconsolata;
        font-size: 38px;
        margin: 0 60px 30px 60px;

        @media (max-width: $mobile) {
          font-size: 28px;
        }
      }

      &__text {
        font-family: $open-sans;
        font-size: 21px;
        margin: 0 60px 60px 60px;
        line-height: 30px;

        @media (max-width: $tablet) {
          font-size: 16px;
          line-height: 25px;
        }

        @media (max-width: $mobile) {
          font-size: 14px;
          line-height: 23px;
        }
      }

      &__button {
        position: absolute;
        bottom: 60px;
        right: 60px;
      }

      &__image {
        width: 100%;
        @media (max-width: $tablet) {
          width: 100%;
        }
      }
    }
  }

  .cta {
    text-align: center;
    margin: 240px 0 120px 0;

    &__button {
      text-decoration: none;
      color: #FFFFFF;
      background: none;
      border: 3px solid #FFFFFF;
      border-radius: 100px;
      padding: 29px 105px;
      font-family: $inconsolata;
      font-weight: bold;
      font-size: 21px;
      transition: border .2s ease-in-out, background .2s ease-in-out, color .2s ease-in-out;

      &:hover {
        border: 3px solid rgb(197, 145, 0);
        background: rgb(197, 145, 0);
        color: #212121;
      }

      @media (max-width: $mobile) {
        font-size: 18px;
        padding: 29px 50px;
      }
    }
  }

  .about {
    margin: 0 64px 120px 64px;

    &__heading {
      font-family: $inconsolata;
      font-size: 38px;
      padding-top: 120px;
      margin-bottom: 24px;
      text-align: center;

      @media (max-width: $mobile) {
        font-size: 28px;
      }
    }

    &__description {
      font-family: $open-sans;
      font-size: 21px;
      text-align: center;
      margin-bottom: 60px;
      color: rgba(255,255,255,0.5);

      @media (max-width: $mobile) {
        font-size: 14px;
        line-height: 23px;
      }
    }

    .person {
      display: grid;
      grid-template-columns: 227px 1fr;
      grid-column-gap: 60px;
      max-width: 961px;
      width: 100%;
      align-items: center;
      margin: 0 auto;

      @media (max-width: $tablet) {
        grid-template-columns: 1fr;
        grid-row-gap: 60px;
        justify-items: center;
      }

      &__position {
        font-family: $open-sans;
        font-weight: bold;
        font-size: 21px;
        opacity: .7;

        @media (max-width: $tablet) {
          font-size: 16px;
        }

        @media (max-width: $mobile) {
          font-size: 14px;
          line-height: 23px;
        }
      }

      &__title {
        font-family: $inconsolata;
        font-size: 48px;
        font-weight: bold;
        margin: 0 0 24px 0;

        @media (max-width: $mobile) {
          font-size: 28px;
        }
      }

      &__text {
        font-family: $open-sans;
        font-size: 21px;
        line-height: 30px;

        @media (max-width: $tablet) {
          font-size: 16px;
          line-height: 25px;
        }

        @media (max-width: $mobile) {
          font-size: 14px;
          line-height: 23px;
        }
      }
    }
  }

  .contact {
    background: #FFFFFF;
    color: #212121;
    padding: 120px 64px;
    text-align: center;

    &__heading {
      font-family: $inconsolata;
      font-size: 38px;
      margin: 0 0 24px 0;

      @media (max-width: $mobile) {
        font-size: 28px;
      }
    }

    &__description {
      font-family: $open-sans;
      font-weight: 21px;
      margin-bottom: 60px;
      opacity: .5;

      @media (max-width: $mobile) {
        font-size: 14px;
        line-height: 23px;
      }
    }

    &__form {
      max-width: 1088px;
      width: 100%;
      margin: 0 auto;
      position: relative;
    }

    &__email {
      width: 100%;
      padding: 26px 60px;
      border: 3px solid #212121;
      border-radius: 100px;
      font-family: $open-sans;
      font-size: 21px;
      font-weight: bold;

      @media (max-width: $tablet) {
        padding: 26px 30px;
        font-size: 18px;
        margin-bottom: 30px;
      }

      @media (max-width: $mobile) {
        font-size: 14px;
        line-height: 23px;
      }
    }

    &__email::placeholder {
      color: #212121;
      opacity: .3;

      @media (max-width: $mobile) {
        text-align: center;
      }
    }

    &__submit {
      position: absolute;
      top: 0;
      right: 0;
      padding: 26px 99px;
      border: 3px solid #212121;
      border-radius: 100px;
      background: #212121;
      color: #FFFFFF;
      font-family: $inconsolata;
      font-weight: bold;
      font-size: 21px;
      cursor: pointer;
      transition: border .2s ease-in-out, background .2s ease-in-out, color .2s ease-in-out;

      &:hover {
        border: 3px solid rgb(197, 145, 0);
        background: rgb(197, 145, 0);
        color: #212121;
      }

      @media (max-width: $tablet) {
        padding: 26px 50px;
        font-size: 18px;
        position: relative;
      }
    }
  }
}

.footer {
  margin: 60px 140px;
  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-columns: 1fr 1fr;

  @media (max-width: $small-desktop) {
    margin: 60px 64px;
  }

  @media (max-width: $mobile) {
    grid-template-columns: 1fr;
    justify-items: center;
  }

  &__text {
    font-family: $inconsolata;
    font-size: 21px;

    @media (max-width: $mobile) {
      font-size: 14px;
    }
  }

  &__list {
    justify-self: end;

    @media (max-width: $mobile) {
      justify-self: center;
      padding: 0;
    }
  }

  &__item {
    display: inline-block;
    margin-left: 60px;
    cursor: pointer;

    @media (max-width: $small-desktop) {
      margin-left: 30px;
    }

    @media (max-width: $mobile) {
      margin: 0 15px;
    }
  }
}

.headerSub {
  display: grid;
  align-content: center;
  justify-items: center;
  position: relative;

  .navigation {
    background: #212121;
    position: fixed;
    top: 0;
    width: 100%;
    display: grid;
    grid-auto-flow: column;
    align-items: center;
    padding: 25px 0;
    z-index: 9999;

    @media (max-width: $small-desktop) {
      grid-auto-flow: row;
    }

    &__logo {
      padding-left: 140px;
      text-decoration: none;
      color: #FFFFFF;
      font-family: $inconsolata;
      font-size: 48px;
      font-weight: bold;

      @media (max-width: $small-desktop) {
        padding-left: 60px;
      }

      @media (max-width: $tablet) {
        font-size: 38px;
      }

      @media (max-width: $mobile) {
        font-size: 28px;
      }
    }

    &__list {
      list-style-type: none;
      justify-self: end;
      margin-right: 140px;
    }

    @media (max-width: $small-desktop) {
      &__list {
      display: none;
      justify-self: center;
      text-align: center;
      margin-right: 0;
      padding: 0;
      }

      .navigation__list--visible {
        display: block;
      }
    }

    &__item {
      display: inline-block;
      margin-right: 60px;

      @media (max-width: $small-desktop) {
        display: block;
        margin-right: 0;
        margin-top: 40px;
      }
    }

    &__item:last-child {
      margin-right: 0;

      @media (max-width: $small-desktop) {
        margin-top: 60px;
      }
    }

    &__link {
      text-decoration: none;
      color: #FFFFFF;
      font-family: $inconsolata;
      font-size: 21px;
      transition: font-weight .2s ease-in-out, color .2s ease-in-out;

      &--button {
        border: 3px solid #FFFFFF;
        border-radius: 100px;
        padding: 14px 30px;
        transition: font-weight .2s ease-in-out, background .2s ease-in-out, color .2s ease-in-out;

        &:hover {
          background: #FFFFFF;
          color: #212121;
          font-weight: bold;
        }
      }

      &:hover:not(.navigation__link--button) {
        font-weight: bold;
        color: rgb(197, 145, 0);
      }
    }

    &__hamburger {
      font-family: $inconsolata;
      font-size: 21px;
      color: #FFFFFF;
      background: none;
      border: none;
      width: 24px;
      height: 24px;
      position: absolute;
      top: 45px;
      right: 60px;
      cursor: pointer;

      @media (min-width: $small-desktop) {
        display: none;
      }

      @media (max-width: $tablet) {
        top: 30px;
      }
    }
  }
}

.subpage {
  background: #FFFFFF;
  color: #212121;
  margin-top: 100px;

  @media (max-width: $mobile) {
    margin-top: 82px;
  }

  .article {
    color: #212121;

    &__image {
      width: 100%;
    }

    &__content {
      max-width: 1216px;
      width: 100%;
      margin: 0 auto;
      padding: 60px 64px;
    }

    &__type {
      font-family: $open-sans;
      font-size: 21px;
      font-weight: bold;
      opacity: .5;

      @media (max-width: $tablet) {
        font-size: 16px;
      }

      @media (max-width: $mobile) {
        font-size: 14px;
      }
    }

    &__heading {
      font-family: $inconsolata;
      font-size: 38px;
      font-weight: bold;
      margin-top: 0;
      margin-bottom: 30px;


      @media (max-width: $mobile) {
        font-size: 28px;
      }
    }

    &__text {
      font-family: $open-sans;
      font-size: 21px;
      line-height: 30px;

      &--first {
        font-weight: bold;
      }

      @media (max-width: $tablet) {
        font-size: 16px;
        line-height: 25px;
      }

      @media (max-width: $mobile) {
        font-size: 14px;
        line-height: 23px;
      }
    }

    &__buttons {
      margin: 120px 0 60px 0;
      text-align: right;

      @media (max-width: $tablet) {
        text-align: center;
      }
    }

    &__button {
      font-family: $inconsolata;
      font-size: 21px;
      font-weight: bold;
      border: 3px solid #212121;
      border-radius: 100px;
      text-decoration: none;
      cursor: pointer;

      &--first {
        padding: 29px 96px;
        background: #FFFFFF;
        color: #212121;
        opacity: .5;
        transition: opacity .2s ease-in-out;

        &:hover {
          opacity: 1;
        }

        @media (max-width: $mobile) {
          margin-bottom: 30px;
        }
      }

      &--last {
        padding: 29px 34px;
        margin-left: 60px;
        color: #FFFFFF;
        background: #212121;
        border-color: none;
        transition: border .2s ease-in-out, background .2s ease-in-out, color .2s ease-in-out;

        &:hover {
          background: rgb(197, 145, 0);
          border-color: rgb(197, 145, 0);
          color: #212121;
        }

        @media (max-width: $mobile) {
          margin-left: 0;
        }
      }

      @media (max-width: $mobile) {
        display: block;
        font-size: 18px;
      }
    }
  }
}